import React from 'react';
import CheckoutPage from '../containers/CheckoutPage';
const HomePage = React.lazy(() => import('../containers/HomPage'))
const LoginPage = React.lazy(() => import('../containers/LoginPage'))
const RegisterPage = React.lazy(() => import('../containers/RegisterPage'))
const ShopPage = React.lazy(() => import('../containers/ShopPage'))
const CartPage = React.lazy(() => import('../containers/CartPage'))
const NotFoundPage = React.lazy(() => import('../containers/NotFoundPage'))
const ProductDetailsPage = React.lazy(() => import('../containers/ProductDetailsPage'))
const routes = [
  {
    component: HomePage,
    extra: true,
    path: '/boxshop'
  },
  {
    component: ShopPage,
    extra: true,
    path: '/boxshop/danh-muc/:slug'
  },
  {
    component: LoginPage,
    extra: true,
    path: '/boxshop/dang-nhap',
    isPrivate: true
  },
  {
    component: RegisterPage,
    extra: true,
    path: '/boxshop/dang-ky',
    isPrivate: true
  },
  {
    component: CartPage,
    extra: true,
    path: '/boxshop/gio-hang',
    isPrivate: false
  },
  {
    component: ProductDetailsPage,
    extra: true,
    path: '/boxshop/san-pham/:slug',
    isPrivate: false
  },
  {
    component: CheckoutPage,
    extra: true,
    path: '/boxshop/thanh-toan',
    isPrivate: false
  },
  {
    component: NotFoundPage,
    extra: true,
    path: '*',
    isPrivate: false
  },

]

export default routes